@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
.calendar{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%;max-width:22rem;padding:2rem;padding-bottom:1rem;border-radius:2rem 2rem;background:#efedd7;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.calendar .header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;height:3rem}.calendar .header button{border:none;width:2rem;background:none;font-size:1.5rem;cursor:pointer;color:#757575;text-align:left;padding-top:.5rem}.calendar .header button:hover{color:#3c3c3c}.calendar .header button:nth-child(2){text-align:right}.calendar .header h3{font-weight:400;padding:0 .5rem}.calendar .header h3 span{font-size:.75rem}.calendar .dates{z-index:1;min-height:3.5rem}.calendar .row{display:grid;grid-template-columns:repeat(7, 1fr);transition:height .4s ease-in-out;font-size:1rem;overflow:hidden;height:3.5rem}.calendar .row.--names{z-index:9;height:2.5rem !important;background:#efedd7;opacity:1 !important}.calendar .row.--active{height:3.5rem !important;opacity:1 !important}.calendar .row .day{font-weight:500;padding:1rem .25rem;border-radius:1.5rem;text-align:center;cursor:pointer;-webkit-tap-highlight-color:transparent;height:3.5rem;background:rgba(255,255,255,0);transition:all .2s ease-in-out}.calendar .row .day.--name{color:#757575;font-weight:400;font-size:.8rem;cursor:default;border-radius:0;padding:.75rem .5rem;height:2.5rem}.calendar .row .day.--today{background:rgba(207,193,140,.3)}.calendar .row .day.--selected{background:#cfc18c}.calendar .row .day.--month-not{font-weight:400;color:#757575}.calendar.min-true .row:not(.--active){height:0}.calendar.min-true .dates{-webkit-animation:none;animation:none}.calendar.min-true .dates.exit-up{-webkit-animation:none;animation:none}.calendar.min-true .dates.exit-down{-webkit-animation:none;animation:none}
#agenda .resize{width:100%;z-index:1;position:absolute;top:0;left:0;padding:1rem;padding-bottom:4rem;touch-action:none;cursor:-webkit-grab;cursor:grab}#agenda .resize div{border-radius:3rem;background:#cfc18c;margin:auto;width:2rem;height:4px;transition:-webkit-filter .1s ease-in-out;transition:filter .1s ease-in-out;transition:filter .1s ease-in-out, -webkit-filter .1s ease-in-out}#agenda .resize:hover div{-webkit-filter:brightness(0.8);filter:brightness(0.8)}#agenda .header{padding:0 2rem;position:-webkit-sticky;position:sticky;top:0}#agenda .header h5{font-size:1rem;font-weight:600}#agenda .header hr{margin:1rem 0;border:0;height:0;border-top:1px solid rgba(0,0,0,.1);border-bottom:1px solid rgba(255,255,255,.3)}#agenda .events{height:100%;overflow:auto;padding:0 2rem;padding-bottom:1rem}#agenda .events .interval{position:relative;margin:1.5rem 0}#agenda .events .interval:last-child{padding-bottom:2rem}#agenda .events .interval .time{font-size:.8rem;font-weight:500;color:#757575;position:relative;display:inline-block;width:2.5rem;position:absolute}#agenda .events .interval .time::after{content:"";height:1px;width:2rem;position:absolute;left:-2rem;top:-0.5rem;background-color:#cfc18c}#agenda .events .interval .event{margin:1rem 0 1rem 3rem;padding:1rem 1.25rem;background-color:#279f71b2;color:#fcfcfc;border-radius:1.25rem;font-size:1rem}#agenda .events .interval .event .title{font-weight:500}#agenda .events .interval .event .duration{font-weight:300}
*{margin:0;padding:0;box-sizing:border-box}:root{font-size:16px;overflow:hidden}body{font-size:18px;font-family:"Noto Sans JP",sans-serif;background-color:#292c35;color:#ebebeb}body #root{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:100vh}header{padding:1rem 2rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}header h2{font-weight:500}header .menu{font-size:2rem}#agenda{background:#fff;color:#303546;padding:2rem 0;margin-top:-5rem;border-radius:2rem 2rem 0 0;box-shadow:0px 0px 20px rgba(0,0,0,.1);-webkit-flex:1 1;flex:1 1;position:relative;z-index:9;overflow:hidden}.calendar-container{background:#efedd7;color:#3c3c3c;border-radius:2rem 2rem 0 0;padding-bottom:5rem;text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;position:relative}.calendar-container::after{content:"";background:#efedd7;height:10rem;width:100%;position:absolute;top:100%}
